.lottery-status {
  margin: auto;
}
.lottery-status-icon {
  width: 20px;
  height: 20px;
  border-radius: 10px;
  display: inline-block;
  vertical-align: text-bottom;
}
.lottery-status-icon-red {
  background-color: #e5003a;
}
.lottery-status-icon-green {
  background-color: green;
}
.my-lottery-color {
  background-color: lightgreen;
}




























































































































































































































































































































































































































































































































































